.App {
	max-width: 1200px;
	padding: 0 1rem;
	margin: 0 auto;
}

.row {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.column {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
	width: 500px;
}

.text-center {
	text-align: center;
}

.column input {
	border-radius: 3px;
	margin: 10px 0;
	padding: 5px;
	border: 1px solid rgba(0, 0, 0, 0.43);
	font-size: 16px;
	width: 80%;
}

.column label {
	text-align: center;
	font-size: 30px;
	font-weight: 200;
}

.header {
	text-align: center;
	font-size: 30px;
	font-weight: 200;
}

.avatar {
	width: 150px;
	height: 150px;
	border-radius: 50%;
}

.button {
	color: #e6e6e6;
	background-color: #0a0a0a;
	border: none;
	font-size: 16px;
	border-radius: 3px;
	width: 200px;
	text-align: center;
	display: block;
	padding: 1rem 0;
	margin: 1rem auto;
}

.button:hover:enabled {
	background: -webkit-gradient(linear, left top, left bottom, from(#1a1a1a), to(#0a0a0a));
	background: -webkit-linear-gradient(#1a1a1a, #0a0a0a);
	background: -o-linear-gradient(#1a1a1a, #0a0a0a);
	background: linear-gradient(#1a1a1a, #0a0a0a);
	color: #fff;
	text-decoration: none;
}

.button:active {
	-webkit-transform: translateY(1px);
	    -ms-transform: translateY(1px);
	        transform: translateY(1px);
}



